 font-weight: normal;\r\n  font-style: normal;\r\n}\r\n@font-face{\r\n  font-family: 'TT Lakes-Regular';\r\n  src: url('@assets/fonts/TTLakes/TTLakes-Regular.eot');\r\n  src: url('@assets/fonts/TTLakes/TTLakes-Regular.woff') format('woff'),\r\n  url('@assets/fonts/TTLakes/TTLakes-Regular.ttf') format('truetype');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}\r\n@font-face{\r\n  font-family: 'TT Lakes-Bold';\r\n  src: url('@assets/fonts/TTLakes/TTLakes-Bold.eot');\r\n  src: url('@assets/fonts/TTLakes/TTLakes-Bold.woff') format('woff'),\r\n  url('@assets/fonts/TTLakes/TTLakes-Bold.ttf') format('truetype');\r\n  font-weight: normal;\r\n  font-style: normal;\r\n}","@import 'normalize.css';\n@import \"swiper-bundle.css\";\n\n@import 'style/abstract/fonts';\n@import 'style/abstract/constans';\n\n// base\n@import 'style/base/base';\n@import 'style/base/buttons';\n\n// layouts\n@import 'style/layouts/header';\n@import 'style/layouts/container';\n@import 'style/layouts/footer';\n\n// components\n@import \"style/components/aside\";\n@import 'style/components/about';\n@import \"style/components/repair-brand\";\n@import \"style/components/technics\";\n@import \"style/components/price\";\n@import \"style/components/side-bar\";","body{\r\n  margin: 0 auto;\r\n}\r\n\r\n.main{\r\n  position: relative;\r\n  background-color: $color-background-section;\r\n  @media screen and (min-width: 1439px){\r\n    display: flex;\r\n  }\r\n}\r\n\r\n.section__block {\r\n  width: 100%;\r\n  max-width: 768px;\r\n  background-color: $color-background-section;\r\n  border-bottom: 1px solid $color-hr;\r\n  @media screen and (min-width: 767px) and (max-width: 1119px){\r\n    max-width: 1119px;\r\n  }\r\n  @media screen and (min-width: 1119px) {\r\n    max-width: 1120px;\r\n  }\r\n}\r\n\r\n.section__block__head{\r\n  background: linear-gradient(90deg, $color-background-section 0%, $color-base 98.61%);\r\n}\r\n\r\n.section__block__title {\r\n  margin: 0;\r\n  color: $color-primary;\r\n  font-family: $font-family-title;\r\n  font-weight: lighter;\r\n  font-size: 16px;\r\n  line-height: 24px;\r\n  letter-spacing: -0.2px;\r\n  text-transform: uppercase;\r\n  padding: 16px;\r\n  @media screen and (min-width: 767px) and (max-width: 1119px){\r\n    padding-left: 24px;\r\n  }\r\n  @media screen and (min-width: 1119px){\r\n    padding-left: 32px;\r\n  }\r\n}","$color-secondary: #1B1C21;\r\n$color-base: #ffffff;\r\n$color-primary: #7E7E82;\r\n$color-background: #EAEAEA;\r\n$color-background-section: #F8F8F8;\r\n$color-icons: #FF3E79;\r\n$color-hr: #D9FFF5;\r\n$color-divider-border: #41F6D7;\r\n$color-button-focus: #B8FFEC;\r\n$color-circle: #DDDDDD;\r\n$color-circle-active: #B5B6BC;\r\n\r\n$font-family-base: 'TT Lakes-Medium', sans-serif;\r\n$font-family-title: 'TT Lakes-Regular', sans-serif;\r\n$font-family-head: 'TT Lakes-Bold', sans-serif;\r\n\r\n%slide{\r\n  background-color: $color-base;\r\n  border: 1px solid $color-background;\r\n  border-radius: 6px;\r\n  display: flex;\r\n  justify-content: space-between;\r\n}",".main__button{\r\n  height: 40px;\r\n  white-space: nowrap;\r\n  background-color: unset;\r\n  border: none;\r\n  cursor: pointer;\r\n  font-family: $font-family-base;\r\n  font-size: 16px;\r\n  line-height: 24px;\r\n  letter-spacing: 0.53px;\r\n  color: $color-primary;\r\n  padding: 8px 9.3px 8px 16.54px;\r\n}\r\n\r\n.main__button--active{\r\n  background-color: $color-base;\r\n  border: 2px solid $color-button-focus;\r\n  border-radius: 6px;\r\n  @media screen and (min-width: 1119px){\r\n    background-color: unset;\r\n    border: none;\r\n    border-radius: 0;\r\n  }\r\n}\r\n\r\n.main__button--desktop {\r\n  display: none;\r\n  @media screen and (min-width: 1119px) {\r\n    display: block;\r\n  }\r\n  &:nth-child(1){\r\n    @media screen and (min-width: 1119px) {\r\n      background-color: $color-base;\r\n      border: 2px solid $color-button-focus;\r\n      border-radius: 6px;\r\n    }\r\n  }\r\n}\r\n\r\n.main__button--desktop--off {\r\n  @media screen and (min-width: 1119px) {\r\n    display: none;\r\n  }\r\n}\r\n\r\n\r\n.button__card{\r\n  width: 40px;\r\n  height: 40px;\r\n  border: 2px solid $color-icons;\r\n  border-radius: 20px;\r\n  background-color: #ffffff;\r\n  background-image: url(\"@assets/icons/dropdown.svg\");\r\n  background-repeat: no-repeat;\r\n  background-position: center;\r\n  margin: 16px;\r\n  cursor: pointer;\r\n}\r\n\r\n.price__button{\r\n  margin-top: 10px;\r\n  width: 119px;\r\n  height: 32px;\r\n  border: none;\r\n  padding-left: 12px;\r\n  color: $color-base;\r\n  border-radius: 16px;\r\n  background-color: $color-icons;\r\n  background-image: url(\"@assets/icons/dropdown-white.svg\");\r\n  background-repeat: no-repeat;\r\n  background-position-x: 85%;\r\n  background-position-y: center;\r\n  font-family: $font-family-head;\r\n  font-size: 12px;\r\n  line-height: 24px;\r\n  text-align: left;\r\n  letter-spacing: -0.15px;\r\n  text-transform: uppercase;\r\n}",".header__icon{\r\n  width: 40px;\r\n  height: 40px;\r\n  border-radius: 20px;\r\n  background-color: $color-icons;\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n.divider{\r\n  width: 2px;\r\n  height: 32px;\r\n  background-color: $color-background;\r\n  border-radius: 1px;\r\n  align-self: center;\r\n  @media screen and (min-width: 1119px){\r\n      display: none;\r\n  }\r\n}\r\n\r\n.divider__tablet{\r\n  display: none;\r\n  @media screen and (min-width: 767px) and (max-width: 1119px){\r\n      display: block;\r\n      margin: 0 20px;\r\n  }\r\n}\r\n\r\n.logo{\r\n  margin: 0 16.73px;\r\n  @media screen and (min-width: 767px) and (max-width: 1119px){\r\n      margin: 0;\r\n  }\r\n  @media screen and (min-width: 1119px){\r\n      display: none;\r\n  }\r\n  @media screen and (min-width: 1439px){\r\n    display: block;\r\n    margin: 0;\r\n  }\r\n}\r\n\r\n/* header tools*/\r\n\r\n.header__tools{\r\n  display: flex;\r\n  margin-left: auto;\r\n}\r\n\r\n.menu{\r\n  background-image: url(\"@assets/icons/burger.svg